/* App.css */
/* Add these styles to your existing App.css */




@keyframes borderColorChange {
    0% { border-color: #007bff; }
    25% { border-color: #00bfff; }
    50% { border-color: #9400d3; }
    75% { border-color: #ff69b4; }
    100% { border-color: #007bff; }
}

/* For responsive design */
@media (max-width: 600px) {
    .header-container {
        flex-direction: column;
        gap: 15px;
    }

    .profile-container {
        flex-direction: column;
        text-align: center;
        gap: 10px;
    }

    .headshot {
        width: 80px;
        height: 80px;
    }
}



body {
  background-color: #fdf6ee; /* soft beige */
  color: #2c2c2c; /* near-black text for readability */
  font-family: 'Helvetica Neue', sans-serif;
  line-height: 1.6;
  margin: 0;
  padding: 0;
}

.App {
    background-color: white; /* Keep content area white */
    max-width: 1200px;
    margin: 0 auto;
    min-height: 100vh;
    padding: 20px;
    box-shadow: 0 0 20px rgba(0,0,0,0.1); /* Optional: adds depth */
}



nav a {
    margin-left: 20px;
    text-decoration: none;
    color: #333;
}

nav a:hover {
    color: #007bff;
}

section {
    padding: 40px 0;
    border-bottom: 1px solid #eee;
}

.project {
    margin-bottom: 20px;
    padding: 15px;
    border: 1px solid #ddd;
    border-radius: 5px;
}

footer {
    text-align: center;
    padding: 20px 0;
    margin-top: 40px;
    color: #666;
}

button {
    display: block;
    margin: 20px auto; /* Centers the button horizontally */
    padding: 10px 20px;
    font-size: 16px;
    color: white;
    background-color: #007bff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

button:hover {
    background-color: #0056b3; /* Darker blue on hover */
}

button:active {
    background-color: #003f7f; /* Even darker blue when clicked */
}

.chat-container {
    margin-top: 20px;
    width: 100%; /* Ensures the container spans the full width */
}

.chat-container label {
    display: block; /* Ensures the label takes up the full width */
    text-align: center; /* Centers the text horizontally */
    font-weight: bold; /* Optional: Makes the text bold */
    margin-bottom: 10px; /* Adds spacing below the label */
}

.chat-input-wrapper {
    display: flex;
    align-items: center;
    gap: 10px;
    width: 100%; /* Ensures the wrapper spans the full width */
}

.chat-input-wrapper input {
    flex: 1; /* Makes the input field take up all available space */
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ddd;
    border-radius: 5px;
    width: 100%; /* Ensures the input spans the full width of its container */
    box-sizing: border-box; /* Includes padding and border in width calculation */
}

.chat-input-wrapper button {
    padding: 10px 20px;
    font-size: 16px;
    color: white;
    background-color: #007bff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    flex-shrink: 0; /* Prevents the button from shrinking */
}

.chat-input-wrapper button:hover {
    background-color: #0056b3;
}

.chat-input-wrapper button:active {
    background-color: #003f7f;
}

.resume-link {
    display: block; /* Makes the link a block element */
    text-align: center; /* Centers the text horizontally */
    margin-bottom: 20px; /* Adds spacing below the link */
    font-size: 26px;
    color: #0022ff;
    text-decoration: none;
    font-weight: bold;
}

.resume-link:hover {
    text-decoration: underline;
    color: #0056b3;
}
.header-container {
  position: relative;
  height: 250px; /* Adjust as needed */
  background-color: pink; /* Optional */
}

/* Top-left name */
.top-left-name {
  position: absolute;
  top: 20px;
  left: 20px;
  font-size: 1.5rem;
  font-weight: bold;
}

/* Center the headshot */
.intro-container {
  margin-top: 40px;
  margin-bottom:40px; 
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
}


.headshot { 
  width: 300px;
  height: 300px;
  border-radius: 50%;
  object-fit: cover;
  border: 5px solid;
  transition: border-color 0.5s ease;
}
.welcome-section {
  text-align: center;
  margin-top: 40px;
  padding: 20px;
}

main {
  max-width: 960px;
  margin: 0 auto;
  padding: 2rem;
}

/* === Project Grid Layout === */

.projects-section {
  padding: 2rem;
}

.projects-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
  justify-content: center;
}

.project-card {
  background: #ffffff;
  border-radius: 10px;
  padding: 1.5rem;
  width: 300px;
  box-shadow: 0 0 12px rgba(0, 0, 0, 0.08);
  text-align: left;
  transition: transform 0.2s ease;
}

.project-card:hover {
  transform: translateY(-5px);
}

.project-card img {
  width: 100%;
  height: 180px;
  object-fit: cover;
  border-radius: 8px;
  margin-bottom: 1rem;
}


.project-card h3 {
  margin-top: 0;
  color: #3e2f1c;
}

.project-card p {
  font-size: 0.95rem;
  color: #333;
}

.project-link {
  display: inline-block;
  margin-top: 1rem;
  padding: 0.5rem 1rem;
  background: #007acc;
  color: white;
  border-radius: 5px;
  text-decoration: none;
}

.project-link:hover {
  background-color: #005f99;
}




